

import React from 'react';
import PropTypes from 'prop-types';

const ResumePropTypes = require('../../prop_types/resume');


export class Skill  extends React.Component {
    state = {
        style: {
            background: '#313131'
        }
    }

    render() {
        const style = this && this.state.style

        return (
            <div className="px-8 py-12">
                <h3 className="mt-6 text-lg font-bold text-gray-900 leading-tight">{this.props.title}</h3>
                <p className=''>{this.props.description}</p>
                <div className="px-8 py-12">
                    <ul>
                        {this.props.keywords.map(function (entry, index) {
                            return (
                                <li key={index}>
                                    <span
                                        className={`bar-expand percentage90`}
                                        style={style}/>
                                    <em>{entry}</em>
                                </li>

                            );
                        })}
                    </ul>
                </div>
            </div>
        );
    }
};

Skill.propTypes = {
    title: PropTypes.string.isRequired,
    description: PropTypes.string.isRequired,
    keywords: PropTypes.arrayOf(PropTypes.string).isRequired
}

export default class Skills  extends React.Component {
    render() {
        return (
            <section id='skill' className="px-8 py-12">
                <div >
                    <h1 className="mt-6 text-lg font-bold text-gray-900 leading-tight">
                        <span>Skills</span>
                    </h1>
                </div>
                <div className=''>
                    {this.props.content.skills.map(function (skill, index) {
                        return (
                            <Skill
                                key={index}
                                title={skill.title}
                                description={skill.description}
                                keywords={skill.keywords}/>
                        );
                    })}
                    {/*
                        <Skill title='Languages' content={this.props.content.languages}/>
                    */}
                </div>
            </section>
        );
    }
};

Skills.propTypes = {
    content: PropTypes.shape({
        skills: ResumePropTypes.skillsSet,
        languages: ResumePropTypes.languagesSet
    }).isRequired
}

