

import React from 'react';

import {education, educationSet} from '../../prop_types/resume';
import {Datetime} from '../../utils/datetime';

export class Entry extends React.Component {

    render() {

        const startDate = Datetime.getDisplayFromDate(this.props.entry.startDate);
        const endDate = Datetime.getDisplayFromDate(this.props.entry.endDate);
        return (
            <div className="px-8 py-12">
                <h3 className="mt-6 text-lg font-bold text-gray-900 leading-tight" >{this.props.entry.institution}</h3>
                <p className=''>
                    {this.props.entry.area}
                    <span> &bull; </span>
                    <span className=''>{this.props.entry.summary}</span>
                    <span> &bull; </span>
                    <em className=''>{startDate} - {endDate}</em>
                </p>
            </div>
        );
    }
};

Entry.propTypes = {
    entry: education
}

export default class Education  extends React.Component {

    render() {
        return (
            <section id='education'>
                <div className="px-8 py-12">
                    <h1 className="mt-6 text-lg font-bold text-gray-900 leading-tight">Education</h1>
                </div>
                <div className="px-8 py-12">
                    {this.props.content.map(function (entry, index) {
                        return (
                            <Entry key={index} entry={entry}/>
                        );
                    })}
                </div>
            </section>
        );
    }
};

Education.propTypes = {
    content: educationSet
}

