

import React from 'react';
const ResumePropTypes = require('../../prop_types/resume');

export default class About  extends React.Component {

    render() {
        return (
            <section id="about" className="bg-gray-500 text-white full-width">
                <div className='sm:flex'>
                    <div className="px-8 py-12">
                        <img name="meimage" className="w-48 rounded-lg shadow-xl" src={this.props.content.picture} alt={this.props.content.name}/>
                        <h1 className="mt-6 text-lg font-bold text-gray-900 leading-tight" >Let's enjoy together</h1>
                    </div>
                    <div className="px-8 py-12" >
                        <h2 className="mt-6 text-lg font-bold text-gray-900 leading-tight" >About Me</h2>
                        {this.props.content.summary.map(function (content, index) {
                            return (
                                <p key={index} className="mt-6 text-gray-900" >
                                    {content}
                                </p>
                            );
                        })}
                        <div className="px-8 py-12 flex">
                            <div className=''>
                                <h2 className="mt-6 text-lg font-bold text-gray-900 leading-tight" >Contact Details</h2>
                                <p className=''>
                                    {/*
                                    <span>{this.props.content.location.address}</span>
                                    <br/>
                                    */}
                                    <span className="text-orange-300">{this.props.content.location.city}</span>
                                    <span className="mx-3 text-orange-300">{this.props.content.location.countryCode}</span>
                                    <br/>
                                    <a className="text-orange-800" href={`skype:${this.props.content.phone}`}>
                                        <span>{this.props.content.phone}</span>
                                    </a>
                                    <br/>
                                    <a className="text-orange-900" href={`mailto:${this.props.content.email}`}>
                                        <span>{this.props.content.email}</span>
                                    </a>
                                </p>
                            </div>
                            <div className="px-8 py-12">
                                <a href='/#' className="inline-block px-4 py-2 rounded-lg bg-orange-600 text-white uppercase" onClick={this.props.generatePDF} >
                                    Download Resume
                                </a>
                            </div>
                        </div>
                    </div>
                </div>
            </section>
        );
    }
};


About.propTypes = {
    content: ResumePropTypes.basics
}
