

import React from 'react';

import {projects, projectsSet} from '../../prop_types/resume';
import {Random} from '../../utils/random';
import Modal from './modal';

export class Entry  extends React.Component {

    constructor(props) {
        super(props);        
        this.state = {
            modalOpen: false
        };
        this.handleOpenModal = this.handleOpenModal.bind(this)
        this.handleCloseModal = this.handleCloseModal.bind(this)
    }

    handleOpenModal() {
        return this.setState({
            modalOpen: true
        });
    }

    handleCloseModal() {
        return this.setState({
            modalOpen: false
        });
    }

    render() {
        return (
            <div className="h-20 w-1/3 px-2 py-3 bg-gray-400 border rounded-lg overflow-hidden justify-center hover:bg-green-400"  >
                <div className="px-2 py-3" onClick={this.handleOpenModal} >
                    <h5 className="text-lg font-bold text-gray-900 leading-tight">{this.props.entry.name}</h5>
                    <p>{this.props.entry.category}</p>
                </div>
                <Modal entry={this.props.entry} isOpen={this.state.modalOpen} onRequestClose={this.handleCloseModal}/>
            </div>
        );
    }
};
Entry.propTypes = {
    entry: projects
}

export default class Portfolio  extends React.Component {

    render() {
        const portfolio = Random.shuffleArray(this.props.content).slice(0, 8);
        return (
            <section id='portfolio'>
                <div className="px-8 py-12">
                    <h1 className="mt-12 text-lg font-bold text-gray-900 leading-tight" >Portfolio</h1>
                    <div id='portfolio-wrapper' className='flex-wrap flex justify-between py-12'>
                        {portfolio.map(function (entry, index) {
                            return (
                                <Entry key={index} index={index} entry={entry} />
                            );
                        })}
                    </div>
                </div>
            </section>
        );
    }
};

Portfolio.propTypes = {
    content: projectsSet
}

