

import React from 'react';

import Carousel from 'nuka-carousel';

import {references, referencesSet} from '../../prop_types/resume';

export class Entry  extends React.Component {

    render() {
        return (
            <div className="mt-0 p-20 sm:p-40 pt-0" >
                <blockquote>
                    <p className="text-green-700 text-lg    " >{this.props.entry.reference}</p>
                    <cite className="text-bold">
                        {this.props.entry.name}
                        <span className="block">
                        {`${this.props.entry.position}, ${this.props.entry.company}`}
                        </span>
                    </cite>
                </blockquote>
            </div>
        );
    }
};

Entry.propTypes = {
    entry: references
}

export default class References  extends React.Component {

    render() {
        const carouselConfig = {
            autoplay: true,
            decorators: [],
            framePadding: '10px',
            cellSpacing: 30,
            wrapAround: true
        };
        return (
            <section id='testimonials'>
                <div className="px-8 py-12">
                    <h1 className="mt-6 text-lg font-bold text-gray-900 leading-tight">References</h1>
                </div>
                <div className="px-8 py-12">
                    <div className=''>
                        <Carousel
                            autoplay={carouselConfig.autoplay}
                            decorators={carouselConfig.decorators}
                            wrapAround={carouselConfig.wrapAround}>
                            {this.props.content.map(function (entry, index) {
                                return (
                                    <Entry key={index} entry={entry} />
                                );
                            })}
                        </Carousel>
                    </div>
                </div>
           </section>
        );
    }
};

References.propTypes = {
    content: referencesSet
}

