

import React from 'react';
import PropTypes from 'prop-types';

import {work, workSet} from '../../prop_types/resume';
import BulletPoints from '../bullet_points';
import {Datetime} from '../../utils/datetime';

export class Entry  extends React.Component {

    render() {        
        const { startDate, endDate ,website ,image ,company ,position ,summary ,highlights ,stack } = this.props.entry

        const start = Datetime.getDisplayFromDate(startDate);
        const end = Datetime.getDisplayFromDate(endDate);

        return (
            <div className="m-3 p-6 bg-white border rounded-lg overflow-hidden">
                <a className="dark-link" href={website}>
                    <img src={image} alt="" ></img>
                    <h4 className="text-lg font-bold text-orange-400 leading-tight">{company}</h4>
                </a>
                <div className='font-semibold flex justify-between'>
                    <p>{position}</p>
                    <p className='text-gray-500 text-sm uppercase'>
                    <em className='' >{start}</em> - <em className=''>{end || 'current'}</em>
                    </p>
                </div>
                <div className="p-3">
                    <p className='text-gray-700 text-sm '>{summary}</p>
                    <BulletPoints points={highlights}/>
                </div>
                <div className="flex p-3">
                {stack.map(function (tech, index) {
                        return (
                            <p key={index} className='m-1 p-1 rounded-md bg-gray-600 text-white text-sm '>{tech}</p>
                        );
                })}                    
                </div>
            </div>
        );
    }
};

Entry.propTypes = {
    index: PropTypes.number.isRequired,
    total: PropTypes.number.isRequired,
    entry: work
}

export default class Work  extends React.Component {

    render() {
        const numEntries = this.props.content.length;
        return (
            <section id='work' className='px-8 py-12'>
                <div >
                    <h1 className="mt-6 text-lg font-bold text-gray-900 leading-tight">
                        <span>Work</span>
                    </h1>
                </div>
                <div className=''>
                    {this.props.content.map(function (entry, index) {
                        return (
                            <Entry key={index} index={index} total={numEntries} entry={entry}/>
                        );
                    })}
                </div>
            </section>
        );
    }
};


Work.propTypes = {
    content: workSet
}
